<template>
  <div class="full-wrapper">

    <div class="title">
      <h3></h3>
    </div>
    <div>
      <el-form
        class="demo-form-inline mb20"
        label-width="80px"
      >
        <el-row>
          <el-col
            :lg="4"
            :sm="8"
          >
            <el-form-item
              class=""
              label=""
            >
              <el-select
                placeholder=""
                v-model="searchCondition.productType"
                @change="initData()"
              >
                <el-option
                  v-for="(v, i) in productTypeList"
                  :key="i"
                  :label="v.label"
                  :value="v.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col
            :lg="4"
            :sm="8"
          >
            <el-form-item label="">
              <el-select
                placeholder=""
                v-model="searchCondition.orderType"
                @change="initData()"
              >
                <el-option
                  v-for="(v, i) in orderTypeList"
                  :key="i"
                  :label="v.label"
                  :value="v.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col
            :lg="4"
            :sm="8"
          >
            <el-form-item label="">
              <el-select
                placeholder=""
                v-model="searchCondition.payType"
                @change="initData()"
              >
                <el-option
                  v-for="(v, i) in payTypeList"
                  :key="i"
                  :label="v.label"
                  :value="v.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col
            :lg="5"
            :sm="8"
          >
            <el-form-item
              class="date-selector-box"
              label=""
            >
              <el-col :span="11">
                <el-date-picker
                  type="date"
                  placeholder=""
                  style="width: 100%;"
                  v-model="searchCondition.startDate"
                  @change="initData()"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </el-col>
              <el-col
                class="line text-center"
                :span="2"
              >-</el-col>
              <el-col :span="11">
                <el-date-picker
                  placeholder=""
                  style="width: 100%;"
                  v-model="searchCondition.endDate"
                  @change="initData()"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </el-col>
            </el-form-item>
          </el-col>
          <el-col
            :lg="5"
            :sm="10"
            class="search-col"
          >
            <el-form-item label-width="0px">
              <div class="search-box">
                <el-input
                  placeholder=""
                  v-model="searchCondition.keyWord"
                  @keyup.enter="initData()"
                ></el-input>
                <el-button
                  type="text"
                  class="el-icon-search"
                  @click="initData()"
                >
                </el-button>
              </div>
            </el-form-item>
          </el-col>
          <el-col
            :lg="1"
            :sm="2"
          >
            <el-form-item label-width="0px">
              <el-button type="primary"> <a
                  type="primary"
                  class="btn"
                  :href="'http://172.22.79.39:8080/a/operate/custOrderItem/export?sign=2&orderType='
                  +searchCondition.orderType+'&productType='+searchCondition.productType+'&status='+searchCondition.payType+'&startDate='
                  +searchCondition.startDate+'&endDate='+searchCondition.endDate+'&orderProduct='+searchCondition.keyWord"
                >Excel</a></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table
      v-loading="loading"
      :data="tableData"
      border
      stripe
      style="width: 100%"
      class="mb20"
    >
      <el-table-column
        prop="custName"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="channel"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="orderId"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="orderProduct"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="orderSlGrade"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="orderBilling"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="orderCycle"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="orderNo"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="addDate"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="statusDate"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="statusLabel"
        label=""
      >
        <template slot-scope="scope">
          <span :class="{'warning':scope.row.status=='1'}">{{scope.row.statusLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="money"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="orderCycle"
        label=""
      ><template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="()=>{dialogVisibleDetail = true; curRow =scope.row;}"
          ></el-button>
          <el-button
            type="text"
            size="small"
            v-if="scope.row.status === '1'"
            @click="()=>{dialogVisibleUpdatePrice = true; curRow =scope.row; moneyMsg= scope.row.money;}"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      v-if="tableData.length>0"
      layout="prev, pager, next"
      :current-page="pageNo"
      :page-size="pageSize"
      :total="total"
      @current-change="initData"
    >
    </el-pagination>
    <el-dialog
      title=""
      :visible.sync="dialogVisibleDetail"
      width="50%"
    >
      <el-form
        ref="form"
        label-width="100px"
      >
        <el-col :span="24">
          <el-form-item label=":">
            <span :class="{'warning':curRow.status=='1'}">{{curRow.statusLabel}}</span>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.orderId}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.addDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.statusDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.orderProduct}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.orderBilling}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.orderNo}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.servStartDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.servEndDate}}
          </el-form-item>
        </el-col>
        <el-col
          :span="24"
          class="mb20"
        >
          <el-form-item label=":">
            {{curRow.money}}
          </el-form-item>
        </el-col>
      </el-form>

      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisibleDetail = false"
        > </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialogVisibleUpdatePrice"
      width="50%"
    >
      <el-form
        ref="form"
        label-width="100px"
      >
        <h3 class="mb20">{{curRow.custName}} {{curRow.phone}} /</h3>
        <el-col :span="24">
          <el-form-item label=":">
            <span :class="{'warning':curRow.status=='1'}">{{curRow.statusLabel}}</span>
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.orderId}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.addDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.statusDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.orderProduct}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.orderBilling}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.orderNo}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.servStartDate}}
          </el-form-item>
        </el-col>
        <el-col :span="11">
          <el-form-item label=":">
            {{curRow.servEndDate}}
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label=":">
            {{curRow.money}}
          </el-form-item>
        </el-col>
        <el-col
          :span="22"
          class="mb20"
        >
          <el-form-item label=":">
            <el-input
              v-model="moneyMsg"
              type="number"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisibleUpdatePrice = false"
        > </el-button>
        <el-button
          type="primary"
          @click="updatePrice()"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "OrderManagement",
  data() {
    return {
      productTypeList: [],
      orderTypeList: [],
      payTypeList: [],
      searchCondition: {
        productType: "",
        orderType: "",
        payType: "",
        keyWord: "",
        startDate: "",
        endDate: ""
      },
      loading: false,
      tableData: [],
      pageNo: 1,
      pageSize: 10,
      total: 0,
      curRow: {},
      dialogVisibleDetail: false,
      dialogVisibleUpdatePrice: false,
      moneyMsg: ""
    };
  },
  mounted() {
    this.initProductSelect();
    this.initOrderSelect();
    this.initPaySelect();
    this.initData(1);
  },
  methods: {
    initProductSelect() {
      this.axios({
        url: "/a/operate/custOrderItem/getProductType",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.productTypeList = res.data.data;
          } else {
            this.$alert(res.data.msg, "", {
              confirmButtonText: ""
            });
          }
        })
        .catch(err => {
          this.$alert("", "", {
            confirmButtonText: ""
          });
        });
    },
    initOrderSelect() {
      this.axios({
        url: " /a/operate/custOrderItem/getOrderType",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.orderTypeList = res.data.data;
            this.orderType = this.orderTypeList[0].vlaue;
          } else {
            this.$alert(res.data.msg, "", {
              confirmButtonText: ""
            });
          }
        })
        .catch(err => {
          this.$alert("", "", {
            confirmButtonText: ""
          });
        });
    },
    initPaySelect() {
      this.axios({
        url: " /a/operate/custOrderItem/getPayStatus",
        method: "post",
        params: {
          sign: 1
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.payTypeList = res.data.data;
          } else {
            this.$alert(res.data.msg, "", {
              confirmButtonText: ""
            });
          }
        })
        .catch(err => {
          this.$alert("", "", {
            confirmButtonText: ""
          });
        });
    },
    initData(pageNo) {
      this.pageNo = pageNo || 1;
      this.axios({
        url: "/a/operate/custOrderItem/findQuery",
        method: "post",
        params: {
          sign: 2,
          orderType: this.searchCondition.orderType,
          productType: this.searchCondition.productType,
          status: this.searchCondition.payType,
          startDate: this.searchCondition.startDate,
          endDate: this.searchCondition.endDate,
          orderProduct: this.searchCondition.keyWord,
          pageNo: this.pageNo,
          pageSize: this.pageSize
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.tableData = res.data.data.data;
            this.total = res.data.data.total;
          } else {
            this.$alert(res.data.msg, "", {
              confirmButtonText: ""
            });
          }
        })
        .catch(err => {
          this.$alert("", "", {
            confirmButtonText: ""
          });
        });
    },
    updatePrice() {
      this.axios({
        url: "/a/operate/custOrderItem/updateMoney",
        method: "post",
        params: {
          sign: 2,
          custOrderId: this.curRow.custOrderId,
          moneyMsg: this.moneyMsg
        }
      })
        .then(res => {
          if (res.data.transCode === "XX00") {
            this.dialogVisibleUpdatePrice = false;
            this.initData(this.pageNo);
          } else {
            this.$alert(res.data.msg, "", {
              confirmButtonText: ""
            });
          }
        })
        .catch(err => {
          this.$alert("", "", {
            confirmButtonText: ""
          });
        });
    },
    changePage() {}
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.search-box {
  float: inherit !important;
  width: auto !important;
  margin: 0 20px;
}
.el-button a {
  text-decoration: none;
  color: #ffffff;
}
.el-form-item {
  margin-bottom: 20px;
}
* #app .title {
  border-bottom: none;
}

form {
  overflow: hidden;
}
</style>



